import { Dropdown } from "primereact/dropdown";
import { useEffect, useState } from "react";
import useProducts from "../../../hooks/ProductHandlers";
import useCompounds, { usePeriodItems } from "../../../hooks/CompoundHandlers";
import './AddCommentModal.css'
import { useRef } from "react";
import api from "../../../services/api";
import { useSWRConfig } from "swr";
import { toast } from "react-toastify";

export const AddCommentModal = (props) => {
    const [type, setType] = useState()
    const [selected, setSelected] = useState()
    const [compound, setCompound] = useState()
    const [period, setPeriod] = useState()
    const commentRef = useRef()
    const { mutate } = useSWRConfig()

    const commentTypes = [
        {label: "Produtos", value: "produto"},
        {label: "Itens", value: "item"},
    ]

    const { products } = useProducts({paginated: false})
    const { compounds } = useCompounds()
    const { items } = usePeriodItems(period)

    useEffect(() => {
        setSelected()
        setCompound()
        setPeriod()
    }, [type])

    const handleSave = () => {
        api.post("comments/", {
            type: type,
            subject: selected,
            content: commentRef.current.value
        }).then(() => {
            mutate("productComments")
            mutate("itemComments")
            props.closeModal()
            toast.success("Comentário adicionado com sucesso!")
        }).catch(() => toast.error("Ocorreu um erro ao adicionar o comentário"))
    }

    return (
        <div id="ac-modal-ct">
            <div id="ac-modal">
                <div className='heading'>
                    <h3>Adicionar comentário</h3>
                    <i className="pi pi-times" onClick={() => props.closeModal()}/>
                </div>
                
                <Dropdown 
                    value={type} 
                    options={commentTypes} 
                    optionLabel="label" optionValue="value"
                    onChange={(e) => setType(e.value)} 
                    placeholder="Selecione o tipo de comentário"
                />

                {type === "produto" &&
                    <Dropdown 
                        value={selected} 
                        options={products} 
                        optionLabel="reference" optionValue="id"
                        onChange={(e) => setSelected(e.value)} 
                        placeholder="Selecione o produto desejado"
                    />
                }

                {type === "item" &&
                    <>
                        <div className="item-specs">
                            <Dropdown 
                                value={compound} 
                                options={compounds} 
                                optionLabel="name"
                                onChange={(e) => setCompound(e.value)} 
                                placeholder="Selecione o COMPOSTO do item"
                            />
                            <Dropdown 
                                value={period} 
                                options={compound?.periods} 
                                optionLabel="name" optionValue="id"
                                onChange={(e) => setPeriod(e.value)} 
                                placeholder="Selecione o PERÍODO do item"
                            />
                        </div>
                        <Dropdown 
                            value={selected} 
                            options={items} 
                            optionLabel="product.reference" optionValue="id"
                            onChange={(e) => setSelected(e.value)} 
                            placeholder="Selecione o item"
                        />
                    </>
                }

                <textarea ref={commentRef} placeholder="O seu comentário vai aqui..."></textarea>

                <button onClick={handleSave}>Salvar</button>
            </div>
        </div>
    )
}