import { useRef, useCallback } from "react"
import { InlineComment } from "../../../../../pages/CommentsPage/InlineComment/InlineComment"
import api from "../../../../../services/api"
import { useInfiniteQuery, useMutation, useQueryClient } from "react-query"
import { toast } from "react-toastify"

import './CompoundProductComments.css'


export const CompoundProductComments = (props) => {
    const page_size = 5
    const fetchPages = ({ pageParam = 0 }) => api.get(`comments?item=${props.itemId}&limit=${page_size}&offset=` + pageParam).then(res => res.data)
    const client = useQueryClient()
    const {
        data,
        error,
        fetchNextPage,
        hasNextPage,
        isFetching,
        isFetchingNextPage,
        status,
    } = useInfiniteQuery(['infiniteCompoundComments', props.itemId], fetchPages, {
        getNextPageParam: (lastPage, pages) => lastPage.next ? pages.length * page_size + 1 : undefined,
    })
    const textRef = useRef()

    const addComment = useMutation(() => api.post("comments/", {
            type: "item",
            subject: props.itemId,
            content: textRef.current.value
        }).then(() => {
            client.invalidateQueries(['infiniteCompoundComments', props.itemId])
            toast.success("Comentário adicionado com sucesso!")
            textRef.current.value = ""
        }).catch(() => toast.error("Ocorreu um erro ao adicionar o comentário"))
    )
    
    const intObserver = useRef()

    const lastPostRef = useCallback((post) => {
        if (isFetchingNextPage) return

        if (intObserver.current) intObserver.current.disconnect()

        intObserver.current = new IntersectionObserver(posts => {
            if (posts[0].isIntersecting && hasNextPage) {
                fetchNextPage()
            }
        })

        if (post) intObserver.current.observe(post)
    }, [isFetchingNextPage, fetchNextPage, hasNextPage])

    if (status === "loading") return <>Carregando comentários...</>
    if (status === "error") return <>Ocorreu um erro tentando retirar os comentários, por favor tente novamente.</>

    return (
        <div className="prod-com-container">
            {data.pages[0].count !== 0 && <h3>Comentários</h3>}
            <div className="add-comment-ct">
                <textarea ref={textRef}/>
                <i className="pi pi-send" onClick={() => addComment.mutate()}/>
            </div>
            {data &&
                data?.pages.map((page, index) => <div key={index}>{
                    page.results.map((comment, index) => {
                        if (page.results.length === index + 1) {
                            return <InlineComment comment={comment} key={comment.id} ref={lastPostRef} noCaller/>
                        }

                        return <InlineComment comment={comment} key={comment.id} noCaller/>
                    })
                }</div>)
            }
            {isFetchingNextPage && <p>Carregando mais comentários...</p>}
        </div>
    )
}